@import '../mixins';

.text-banner {
    padding: 120px 125px;
    @include respond(sm-tab-port){
        padding:96px 16px;
    }

    &__heading {
        color: var(--Wivians-Factory-Gray, #3F3F3E);

        /* Desktop/H1 */
        font-family: Inter Tight;
        font-size: 48px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        letter-spacing: 0.96px;
        margin-bottom: 32px;
    }

    &__paragarph {
        color: var(--Light-Gray-1, #AAA);

        /* Desktop/Body */
        font-family: Inter Tight;
        font-size: 24px;
        font-style: normal;
        font-weight: 400;
        line-height: 30px; /* 125% */
        letter-spacing: 0.48px;
    }

    @include respond(phone){
        &__heading {
            font-size: 32px;
            margin-bottom: 24px;
        }
    
        &__paragarph {
            font-size: 18px;
        }
    }

}